<template>
  <div
    class="lineChart"
    :ref="refName"
    :style="{ width: width, height: height }"
  ></div>
</template>
<script>
// import { fontSize } from '@/utils/common';
const dom = 800;
const barWidth = dom / 20;
export default {
  name: 'StereoBar',
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '100%'
    },
    refName: {
      type: String,
      default: 'chart'
    },
    chartName: {
      type: String,
      default: '计划产量'
    },
    chartData: {
      type: Object,
      default: () => ({
        colors: ['#73fcff', '#86eef1', '#5ad6d9', '#3dc8ca']
      })
    },
    unit: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      data: [],
      loading: false,
      chart: null,
      colorGroup: {
        type: 'linear',
        x: 0,
        x2: 1,
        y: 0,
        y2: 0,
        colorStops: [
          {
            offset: 0,
            color: this.chartData.colors[0] // 最左边
          },
          {
            offset: 0.5,
            color: this.chartData.colors[1] // 左边的右边 颜色
          },
          {
            offset: 0.5,
            color: this.chartData.colors[2]// 右边的左边 颜色
          },
          {
            offset: 1,
            color: this.chartData.colors[3]
          }
        ]
      }
    };
  },
  watch: {
    chartData: {
      handler() {
        this.drawChart();
      },
      deep: true
    }
  },
  methods: {
    drawChart() {
      const option = {
        // backgroundColor: '#010d3a',
        // 提示框
        tooltip: {
          trigger: 'axis',
          formatter: '{b} : {c}',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: '5%',
          right: '5%',
          top: '10%',
          bottom: '8%'
        },
        xAxis: {
          data: this.chartData.xData,
          axisLine: {
            lineStyle: {
              color: '#3eb2e8'
            }
          },
          // 坐标值标注
          axisLabel: {
            show: true,
            margin: 10,
            textStyle: {
              color: '#fff'
            }
          }
        },
        yAxis: {
          // 坐标轴
          axisLine: {
            show: false
          },
          // 坐标值标注
          axisLabel: {
            show: true,
            textStyle: {
              color: '#fff'
            }
          },
          // 分格线
          splitLine: {
            lineStyle: {
              color: '#4784e8',
              opacity: 0.6
            }
          }
        },
        series: [
          {
            type: 'bar',
            barWidth: barWidth,
            itemStyle: {
              normal: {
                color: params => {
                  return this.colorGroup;
                }
              }
            },
            label: {
              show: false,
              position: [barWidth / 2, -(barWidth + 20)],
              color: '#ffffff',
              fontSize: 14,
              fontStyle: 'bold',
              align: 'center'
            },
            data: this.chartData.seriesData
          },
          {
            z: 2,
            type: 'pictorialBar',
            data: this.chartData.seriesData,
            symbol: 'diamond',
            symbolOffset: [0, '50%'],
            symbolSize: [barWidth, barWidth * 0.5],
            itemStyle: {
              normal: {
                color: params => {
                  return this.colorGroup;
                }
              }
            }
          },
          {
            z: 3,
            type: 'pictorialBar',
            symbolPosition: 'end',
            data: this.chartData.seriesData,
            symbol: 'diamond',
            symbolOffset: [0, '-50%'],
            symbolSize: [barWidth, barWidth * 0.5],
            itemStyle: {
              normal: {
                borderWidth: 0,
                color: params => {
                  return this.colorGroup.colorStops[0].color;
                }
              }
            },
            label: {
              show: true,
              position: 'top',
              color: '#ffffff',
              fontSize: 14,
              fontStyle: 'bold',
              align: 'center',
              formatter: params => {
                return params.value + this.unit;
              }
            }
          }
        ]
      };
      this.chart.setOption(option);
      window.addEventListener('resize', () => {
        this.chart.resize();
      });
    },
    init() {}
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs[this.refName]);
    setTimeout(() => {
      this.drawChart();
    }, 500);
  }
};
</script>
<style lang="less" scoped></style>
