<template>
  <div class="kanban">
    <Header titleName="设备稼动率看板"></Header>
    <search-bar @search="search" :searchData="searchData"
      :searchOptions="searchOptions" @getGroupSucceed="loadData"></search-bar>
    <div class="kanbanContent">
      <div class="top_content">
        <card-module title="稼动率" :isExport="false" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
          titleWidth="70%">
          <stereo-bar refName="yieldChart" height="calc((100vh - 15rem) / 2 - 20px)" unit="%" :chartData="yieldData"></stereo-bar>
        </card-module>
      </div>
      <div class="bottom_content">
        <card-module :title="yiledKey" :isExport="false" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
          titleWidth="70%">
          <stereo-bar refName="output" height="calc((100vh - 15rem) / 2 - 20px)" :chartData="outputData"></stereo-bar>
        </card-module>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery';
// import { fontSize } from '@/utils/common';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import CardModule from '@/components/common/kanban/CardModule';
import searchBar from './components/searchBar.vue';
import Header from '@/components/common/kanban/Header';
import StereoBar from '@/components/common/kanban/StereoBar';
// import { exportJson2Excel } from '@/utils/exportExcel';
import { STATUS_CODE } from '@/utils/constantsFile';
// import { formatJson } from '@/utils/publicFunction';
import { getSearchToday } from '@/utils/publicFunction2';
import { dataFormatter } from '@/utils/publicFunction';
// import { prodChartYield } from '@/api/xwReport';
import { getDeviceTimeFluctuationRate, getDeviceTimeCapacityAndEnergyEfficiency, getConfigurationByEnum, getAllGroupAndDeviceOpenApi } from '@/api/kanban';
export default {
  name: 'kanban_xw_DTplato',
  components: {
    // vueSeamlessScroll,
    CardModule,
    searchBar,
    Header,
    StereoBar
  },
  data() {
    return {
      timers: '',
      yieldDeviceName: '裁切检测包装机25305-1',
      title: '',
      groupId: 0,
      time: '',
      dataTime: '',
      tableHeight: 180,
      searchData: {
        groupValue: [1, 6, 7, '00000165_00000001'],
        // date: [],
        date: [getSearchToday(15) + ' 00:00:00', getSearchToday(0) + ' 23:59:59'],
        timeKeyEnum: 'DAY'
      },
      searchOptions: [
        {
          type: 'cascader',
          key: 'groupValue',
          options: []
        },
        {
          type: 'daterange',
          key: 'date',
          options: [
            {
              label: '日',
              value: 'DAY'
            },
            {
              label: '周',
              value: 'WEEK'
            },
            {
              label: '月',
              value: 'MONTH'
            }
          ]
        }
      ],
      tableColumns: [],
      actualTableData: [],
      OKTableData: [],
      NGTableData: [],
      yieldTableData: [],
      kanbanConfig: { title: '', key: '', time: 15000 },
      yieldXData: [],
      planYield: [],
      coatingData: {},
      barChartInfo: {
        current: 1,
        size: 4
      },
      maintainAnalyse: {
        current: 1,
        size: 4
      },
      timer: null,
      yiledKey: '产量',
      outputData: {
        seriesData: [],
        xData: [],
        colors: ['#73fcff', '#86eef1', '#5ad6d9', '#3dc8ca']
      },
      yieldData: {
        seriesData: [],
        xData: [],
        colors: ['#E4CE16', '#E2CF15', '#C1B01A', '#B8A91E']
      }
    };
  },
  mounted() {
    document.documentElement.style.fontSize = this.fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = this.fontSize() + 'px';
    };
    this.loadData();
    this.timer && clearInterval(this.timer);
    this.timer = setInterval(() => {
      this.loadData();
    }, 1000 * 60 * 60);
  },
  created() {
    var that = this;
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig =
        KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    this.getGroupLineDevice();
  },
  destroyed() {
    this.timer && clearInterval(this.timer);
  },
  methods: {
    // 获取 车间 —— 产线 —— 设备
    async getGroupLineDevice() {
      try {
        var param = {
          isProductionDevice: 1
        };
        const res = await getAllGroupAndDeviceOpenApi(param, { secretKey: this.kanbanConfig.key });
        for (var i = 0; i < res.rows.length; i++) {
          if (res.rows[i].code) {
            res.rows[i].id = res.rows[i].code;
          }
        }
        res.rows = dataFormatter(res.rows);
        this.searchOptions[0].options = res.rows;
      } catch (error) {
        console.log(error);
      }
    },
    fontSize(num = 10) {
      const clientWidth =
        window.innerWidth ||
        document.documentElement.clientWidth ||
        document.body.clientWidth;
      if (!clientWidth) return;
      const fontSize = clientWidth / 1920;
      return num * fontSize;
    },
    showLogo() {
      return 'logo_' + this.kanbanConfig.logo;
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return (
        decodeURIComponent(
          (new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(
            url
          ) || ['', ''])[1].replace(/\+/g, '%20')
        ) || null
      );
    },
    // 获取稼动率统计
    async getYieldChartData() {
      // console.log(this.yieldDeviceName);
      try {
        const { timeKeyEnum, date, groupValue } = this.searchData;
        const param = {
          queryDateTypeEnum: timeKeyEnum,
          startTime: date[0],
          endTime: date[1]
        };
        if (groupValue) {
          var str = groupValue[groupValue.length - 1];
          param.deviceCode = str;
        }
        const res = await getDeviceTimeFluctuationRate(param, { secretKey: this.kanbanConfig.key });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          const xData = [];
          const data = [];
          res.rows.forEach(item => {
            xData.push(item.time.substr(5, 5));
            data.push(item.value);
          });
          this.yieldData.seriesData = data;
          this.yieldData.xData = xData;
        } else {}
      } catch (error) {
        console.log(error);
      }
    },
    // 获取产能统计
    async getProductivityChartData() {
      try {
        const { timeKeyEnum, date, groupValue } = this.searchData;
        const param = {
          queryDateTypeEnum: timeKeyEnum,
          startTime: date[0],
          endTime: date[1]
        };
        if (groupValue) {
          var str = groupValue[groupValue.length - 1];
          param.deviceCode = str;
        }
        const res = await getDeviceTimeCapacityAndEnergyEfficiency(param, { secretKey: this.kanbanConfig.key });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // value 加工次数、 value2 用电量
          const date = [];
          const value = [];
          res.rows.forEach(item => {
            date.push(item.time.substr(5, 5));
            value.push(item.value);
          });
          this.outputData.seriesData = value;
          this.outputData.xData = date;
          console.log(this.outputData);
        } else {}
      } catch (error) {
        console.log(error);
      }
    },
    // 获取生产配置
    async getYieldConfig() {
      try {
        const condtion = {};
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        const data = await getConfigurationByEnum(
          {
            configurationTypeEnum: 'PRODUCTION_CONFIGURATION'
          },
          condtion
        );
        if (data.code === '0000') {
          this.yiledKey = data.rows?.processConfiguration || '产量';
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.getProductivityChartData();
      }
    },
    search() {
      this.loadData();
    },
    loadData() {
      this.getYieldChartData();
      this.getYieldConfig();
    }
  }
};
</script>

